import { Component, OnInit } from '@angular/core';

import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';

@Component({
  selector: 'app-careers-open',
  templateUrl: './careers-open.component.html',
  styleUrls: ['./careers-open.component.scss']
})
export class CareersOpenComponent implements OnInit {

  public bMobile = false;

  constructor(breakpointObserver: BreakpointObserver) {
    breakpointObserver.observe('(max-width: 575px)').subscribe(result => {
      this.bMobile = result.matches;
    });
  }

  ngOnInit() {
  }

}
