import { NgModule} from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';


import { SharedModule } from '../shared/shared.module';
import { CareersComponent } from './careers.component';

// 3rd party
import { NgxPageScrollModule } from 'ngx-page-scroll';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { CareersMainComponent } from './careers-main/careers-main.component';
import { CareersJobsComponent, DialogJobDialog } from './careers-jobs/careers-jobs.component';
import { CareersOpenComponent } from './careers-open/careers-open.component';
import {RouterModule, Routes} from "@angular/router";

// Routes
const routes: Routes = [
  {
    path: '',
    component: CareersComponent
  }
];


