import { Component, OnInit, Inject } from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';

import { Ci18nJob } from 'src/app/shared/models/ci18n-job';
import { Ci18nParagraph } from 'src/app/shared/models/ci18n-paragraph';

import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';

export interface DialogData {
  position: string;
  location: string;
  level: string;
  jobdescription: [];
  role: [];
  candidate: [];
}

@Component({
  selector: 'app-careers-jobs',
  templateUrl: './careers-jobs.component.html',
  styleUrls: ['./careers-jobs.component.scss']
})
export class CareersJobsComponent implements OnInit {

  public bError = false;
  public bMobile = false;

  public jobs: Ci18nJob[] = [{
    POSITION: 'Loading...',
    LOCATION: 'Loading...',
    LEVEL: 'Loading...',
    JOBDESCRIPTION: {
      PARAGRAPH: 'Loading...'
    },
    ROLE:  {
      PARAGRAPH: 'Loading...'
    },
    CANDIDATE:  {
      SPAN: 'Loading...',
      PARAGRAPH: 'Loading...'
    },
  }];


  constructor(public translate: TranslateService, public dialog: MatDialog, breakpointObserver: BreakpointObserver) {
    breakpointObserver.observe('(max-width: 575px)').subscribe(result => {
      this.bMobile = result.matches;
    });
  }

  openDialog(index: number): void {
    const dialogRef = this.dialog.open(DialogJobDialog, {
      width: 'calc(100% - 60px)',
      height: 'calc(100% - 60px)',
      data: {index: index, position: this.jobs[index].POSITION,
            location: this.jobs[index].LOCATION,
            level: this.jobs[index].LEVEL,
            jobdescription: this.jobs[index].JOBDESCRIPTION,
            role: this.jobs[index].ROLE,
            candidate: this.jobs[index].CANDIDATE
      }
    });

    dialogRef.afterOpened().subscribe(result => {
      console.log('The dialog was opened');
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }

  ngOnInit() {


    this.translate.get('CAREERS.JOBS.JOB').subscribe((res: Ci18nJob[] | string) => {
      if (typeof(res) === 'string') {
        this.bError = true;
        this.jobs = [{
          POSITION: 'Cannot get translation!',
          LOCATION: 'Cannot get translation!',
          LEVEL: 'Cannot get translation!',
          JOBDESCRIPTION: {
            PARAGRAPH: 'Cannot get translation!'
          },
          ROLE:  {
            PARAGRAPH: 'Cannot get translation!'
          },
          CANDIDATE:  {
            SPAN: 'Cannot get translation!',
            PARAGRAPH: 'Cannot get translation!'
          }
      }];
      } else {
        this.jobs = res as Ci18nJob[];
      }
    });

  }

}


@Component({
  selector: 'dialog-job-dialog',
  templateUrl: './dialog-job-dialog.html',
  styleUrls: ['./dialog-job-dialog.scss']
})
export class DialogJobDialog {

  public bError = false;

  public paragraphs: Ci18nParagraph[] = [{
    PARAGRAPH: 'Loading...',
  }];

  constructor(
    public translate: TranslateService,
    public dialogRef: MatDialogRef<DialogJobDialog>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

  ngOnInit() {
    this.translate.get('CAREERS.JOBS.GENERAL.ABOUT-US.DESCRIPTION').subscribe((res: Ci18nParagraph[] | string) => {
      if (typeof(res) === 'string') {
        this.bError = true;
        this.paragraphs = [{
          PARAGRAPH: 'Cannot get translation!'
      }];
      } else {
        this.paragraphs = res as Ci18nParagraph[];
      }
    });
  }

}
